import "./Message.component.scss";
import React, {useContext} from 'react';
import GameContext from "../../context/Game.context";

export const Message = () => {
	const {isOver, isStopped, type,word} = useContext(GameContext);

	if (!isOver && !isStopped) {
		return null;
	}

	if (type === "SUCCES") {
		return (
			<strong className="message message--succes">Cest tiguidou</strong>
		)   
	}
	if (type === "FAIL") {
		return (
			<strong className="message message--fail">Lche pas la patate : {word}</strong>
		)
	}


	return null;
};

export default Message;